<template>
  <div class="hello">
     <ali-player @play="play"
        :isLive="aplayer.isLive" 
        :source="aplayer.source" 
        :vid="aplayer.vid"
        :autoplay="aplayer.autoplay"
        :useH5Prism="aplayer.useH5Prism"
        :skinLayout = "aplayer.skinLayout"
        :playauth="aplayer.playauth" ref="player">
     </ali-player>
  </div>
</template>

<script>
import VueAliplayer from 'vue-aliplayer'
export default {
   data() {
    return {
      aplayer: {
        source: "http://static.smartisanos.cn/common/video/t1-ui.mp4",
        vid: "8db6e5c7ff5f4257b41e2487ec61d592",
        autoplay: true,
	      isLive:true,
        useH5Prism:true,
	      useFlashPrism:false,
	      cover: 'http://cdnoss.youkouyang.com/cover.png',
        x5_type:'h5',
        x5_fullscreen:true,
        skinLayout:[
          {name:"bigPlayButton", align:"blabs", x:"70", y:"150"},
          {name: "H5Loading", align: "cc"}
          ],
        playauth:
          ""
      },
      player: null
    };
  },
  components: {
    'ali-player': VueAliplayer
  },
  methods: {
      play: function(){
        const player = this.$refs.player.instance
        player && player.play()
      },
      pause: function() {
        const player = this.$refs.player.instance
        player && player.pause()
      },
      replay: function() {
        const player = this.$refs.player.instance
        player && player.replay()
      }
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.prism-player{
  height:700px;
}
.ali-player{
 width:100%;
 height:100%;
 position:absolute;
 top:0;
 left:0;
}
.prism-player{
  width:100%!important;
  height:100%!important;
  position:absolute;
  top:0;
  left:0;
}
.prism-controlbar{
  display:none!important;
}
</style>
<style>
.prism-big-play-btn{
  left:40%!important;
  bottom:30%!important;
}
.prism-player .prism-controlbar{
  z-index:-100!important;
}

</style>



